* {
  margin: 0;
  padding: 0;
  outline: none;
}
.img-thumbnail {
  border: none;
}
html {
  font-size: 10px;
}
body {
  font-size: 16px;
}
.wrapper {
  max-width: 1170px;
  margin: 0 auto;
}
.navbar {
  box-shadow: 0 2px 7px rgba(0, 0, 0, 0.15);
  background-color: #ffffff;
}
.navbar .collapse .navbar-nav {
  width: 100%;
  display: flex;
  justify-content: center;
  position: relative;
}
.navbar .collapse .navbar-nav li {
  margin: 0 2.5rem;
}
.navbar .collapse .navbar-nav li a {
  color: #2b2b2b;
  font-family: 'Roboto Light';
  font-size: 1em;
  font-weight: 300;
}
.navbar .collapse .navbar-nav li a:after {
  display: block;
  content: "";
  height: 1px;
  width: 0%;
  background-color: #39c7f4;
  transition: width 0.2s ease-in-out;
}
.navbar .collapse .navbar-nav li a:hover:after,
.navbar .collapse .navbar-nav li a:focus:after {
  width: 90%;
  margin: 0 auto;
}
.smm {
  padding: 4rem 0;
  background: url(../../build/img/smm-bg.png) center right no-repeat;
}
.smm .smm-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 750px;
}
.smm .smm-box h1 {
  color: #2b2b2b;
  font-family: 'Akrobat Bold';
  font-size: 4.5rem;
  margin-top: 9rem;
  line-height: 72px;
}
.smm .smm-box button {
  margin: 3rem 0;
  color: #ffffff;
  font-family: 'Roboto Regular';
  font-size: 1em;
  font-weight: 400;
  padding: 1.7rem 6rem;
  background-image: linear-gradient(to top, #fb5503 0%, #ffa200 100%);
  box-shadow: 0 3px 7px rgba(0, 0, 0, 0.15);
  border-radius: 12px;
  border: 0;
}
.smm .smm-box button:hover {
  background-image: linear-gradient(to top, #fc661b 0%, #ffab1a 100%);
}
.smm .smm-box span {
  color: #2b2b2b;
  font-family: 'Roboto Medium';
  font-size: 1em;
  font-weight: 500;
  transform: translateX(30%);
  text-align: right;
}
.smm .smm-box span:before {
  content: '';
  display: inline-block;
  height: 28px;
  width: 26px;
  background: url('../../build/img/icon-pointer.png') no-repeat;
  margin-right: 1rem;
}

